import React from "react";
import PropTypes from "prop-types";
import "../styles/pane.css";
import InfoSection from "./InfoSection";

export default class Pane extends React.Component{
    render(){
        return <div className="pane" style={this.props.style}>
            <InfoSection title={this.props.title} icon={this.props.icon}>
                {this.props.children}
            </InfoSection>
        </div>
    }
}
Pane.propTypes = {
    title: PropTypes.string,
    icon: PropTypes.any,
    style: PropTypes.any,
}