import React from "react";
import PropTypes from "prop-types";
import "../styles/profile_info.css";

export default class ProfileInfo extends React.Component{
    render(){
        return <div className="profile-info">
            <img className="profile-pic" src={this.props.image} alt={this.props.firstName + " " + this.props.lastName}/>
            <span className="profile-name">
                <span className="profile-first">
                    {this.props.firstName}
                </span>
                <span className="profile-last">
                {this.props.lastName}
                </span>
            </span>
            <span className="profile-position">
                {this.props.position}
            </span>
        </div>
    }
}
ProfileInfo.propTypes = {
    firstName: PropTypes.string,
    lastName: PropTypes.string,
    image: PropTypes.any,
    position: PropTypes.string
}