import React from "react";
import PropTypes from "prop-types";
import "../styles/edu_xp.css";

export default class EducationRecord extends React.Component{
    render(){
        let x = this.props.active? " active" : "";
        return <div className="edu-xp">
            <div className={"line"+x}/>
            <div className="edu-data">
                <div className="edu-title-year">
                    <span className="edu-title">
                        {this.props.title}
                    </span>
                    <span className="edu-sep">
                        |
                    </span>
                    <span className={"edu-duration"+x}>
                        {this.props.dateStart + " - " + this.props.dateEnd}
                    </span>
                </div>
                <span className="edu-where">
                    {this.props.where}
                </span>
                <div className="edu-title-year">
                    <span className="edu-gpa-title">
                            GPA:
                        </span>
                    <span className="edu-gpa">
                        {this.props.gpa}
                    </span>
                </div>
            </div>
        </div>
    }
}
EducationRecord.propTypes = {
    title: PropTypes.string,
    dateStart: PropTypes.string,
    dateEnd: PropTypes.string,
    where: PropTypes.string,
    gpa: PropTypes.string,
    active: PropTypes.bool
}