import React from "react";
import PropTypes from "prop-types";
import "../styles/info_panel.css";

export default class InfoPanel extends React.Component{
    render(){
        let instance = this;
        if(this.props.titles !== null && this.props.values !== null && this.props.titles.length !== this.props.values.length){
            throw new Error("titles and values must be same length");
        }
        return <div className="info-panel">
            {
                this.props.titles.map(function(title, i) {
                    return [title, instance.props.values[i]];
                  }).map((v, i)=>{
                      let title = v[0];
                      let value = v[1];
                      return <div className="info-item" onClick={()=>{
                          this.props.listener(i);

                      }}>
                                <span className="info-title">
                                    {title}
                                </span>
                                <span className="info-value">
                                    {value}
                                </span>
                             </div>
                  })
            }
        </div>
    }
}
InfoPanel.propTypes = {
    titles: PropTypes.arrayOf(PropTypes.string),
    values: PropTypes.arrayOf(PropTypes.string),
    listener: PropTypes.func
}