import React from "react";
import PropTypes from "prop-types";
import "../styles/research_item.css";

export default class ResearchItem extends React.Component{
    render(){
        let x = this.props.active? " active" : "";
        return <div className="research-xp">
            <div className={"line"+x}/>
            <div className="research-data">
                <div className="research-title-year">
                    <span className="research-title">
                        {this.props.title}
                    </span>
                    <span className="research-sep">
                        |
                    </span>
                    <span className={"research-duration"+x}>
                        {this.props.date}
                    </span>
                </div>
                <span className="research-description">
                    {this.props.description}
                </span>
            </div>
        </div>
    }
}
ResearchItem.propTypes = {
    title: PropTypes.string,
    date: PropTypes.string,
    description: PropTypes.string
}