import React from "react";
import PropTypes from "prop-types";
import "../styles/work_xp.css";

export default class ProjectItem extends React.Component{
    render(){
        let x = this.props.active? " active" : "";
        return <div className="work-xp">
            <div className={"circle"+x}/>
            <div className="work-data">
                <div className="work-title-year">
                    <span className="work-title">
                        {this.props.title}
                    </span>
                    <span className="work-sep">
                        |
                    </span>
                    <span className={"work-duration"+x}>
                        {this.props.date}
                    </span>
                </div>
                <span className="work-description">
                    {this.props.description}
                </span>
                <div className="work-title-year">
                    <span className="work-tech">
                            {/* Skills: */}
                            Technologies Used:
                        </span>
                    <span className="work-techs">
                        {this.props.tech}
                    </span>
                </div>
            </div>
        </div>
    }
}
ProjectItem.propTypes = {
    title: PropTypes.string,
    date: PropTypes.string,
    description: PropTypes.string,
    tech: PropTypes.string,
    active: PropTypes.bool
}